<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-04 10:35:48
 * @LastEditTime: 2019-11-04 14:54:35
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <!-- 标题 -->
    <div class="headTitle">
      <i @click="toTravelinfo" class="cubeic-arrow"></i>
      <p></p>
    </div>
    <p class="title">{{data.displayed_owned_by}}</p>
    <div class="headerimgs progressive">
      <img
        class="headerimg"
        v-progressive="data.insurers_banner"
        src="@/assets/images/loading photo@3x.png"
        alt
      />
    </div>

    <p class="travelead">{{data.name}}</p>

    <p class="budget">{{data.desc1}}</p>

    <div class="travel-insurance">{{data.category_name}}</div>

    <!-- 价格 -->
    <div class="price" v-if="plan">
      <!-- <div class="fontIcon">
        <i class="iconfont icon-shoucang"></i>
        <i class="iconfont icon-fenxiang"></i>
      </div>-->
      {{data.currency_desc ? data.currency_desc : ''}}{{price}}
    </div>
    <div class="projectImg progressive">
      <img
        class="box"
        v-progressive="data.products_imgs[0]"
        src="@/assets/images/loading photo@1X.png"
        alt
      />
    </div>
    <!-- 总天数和总人数 -->
    <div class="people">
      <span class="days">
        <span>{{days}}</span>
        {{$t('Days')}}
      </span>
      <span class="line">|</span>
      <span class="adults">
        <img class="adultsimg" src="@/assets/images/Adults@3x.png" alt />
      </span>
      <span class="adultsnum">{{adults}}</span>
      <span class="friends">
        <img class="friendsimg" src="@/assets/images/friends Copy@3x.png" />
      </span>
      <span class="friendsnum">{{childs}}</span>
      <span class="toTravelinfo" @click="toTravelinfo">
        <i class="cubeic-arrow"></i>
      </span>
    </div>

    <!-- Key Features -->
    <div>
      <p class="key">{{$t('product_decs_key_title')}}</p>
      <div class="key-content">
        <div class="features-wrap">
          <p :class="{'features': flag}">{{data.remarks}}</p>
          <span class="more" @click="clickmore">{{flag ? $t('product_decs_key_more') : $t('hide')}}</span>
        </div>
      </div>
    </div>

    <!-- Customer Service -->
    <div class="customers" @click="customers">
      <span class="starrs">
        <img class="starr" :src="data.cs_logo" alt />
      </span>
      <span class="customer">{{$t('account_customer_service')}}</span>
      <i class="cubeic-arrow"></i>
    </div>

    <!-- Coverage Details -->
    <a
      class="coverages"
      target="_Blank"
      :href="'https://dev-wesurance814.com/wesupromo/webview/coverageDetails?product_id='+product_id+'&language='+language"
    >
      <span class="covers">
        <img class="cover" src="@/assets/images/Confirmation of cover@3x.png" alt />
      </span>
      <span class="coverage">{{$t('products_choose_plan_coverage_title1')}}</span>
      <i class="cubeic-arrow"></i>
    </a>

    <div class="button-bar">
      <button class="proceed" @click="proceed">{{$t('products_proceed')}}</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      more: false,
      data: "",
      flag: true,
      days: "",
      price: "",
      errorInfo: "" //报价错误提示
    };
  },
  computed: {
    ...mapState([
      "travelInfo",
      "product_id",
      "language",
      "plan",
      "userPhone",
      "MerchantId",
      "pricedata"
    ]),
    //计算成人和小孩的人数
    adults() {
      return (
        this.travelInfo.count.myself +
        this.travelInfo.count.spouse +
        this.travelInfo.count.friends
      );
    },
    childs() {
      return this.travelInfo.count.child;
    }
  },
  methods: {
    ...mapMutations(["setLogo", "setPrice"]),
    //点击更多显示更多
    clickmore() {
      this.flag = !this.flag;
    },
    //点击Customer Service事件
    customers() {
      this.$createDialog({
        type: "alert",
        confirmBtn: { text: "確認", active: true, href: "javascript:;" },
        content: this.data.customer_service
        // '如有查询，请联络Starr:<a href="mailto:(852)37655566/cs.hk@starrcompanies.com">(852)37655566/cs.hk@starrcompanies.com</a>(办公时间：星期一至星期五，上午9:00至12:30；下午1:30至下午5:00)'
      }).show();
    },
    //点击Coverage Details事件
    // coverage() {
    //   this.$router.push({
    //     path: "/CoverageDetails",
    //     query: { product_id: this.product_id }
    //   });
    // },
    //点击Proceed事件
    proceed() {
      //如果已经登录过就不显示这个页面
      // if (this.userPhone) {
      //   this.$router.push("/Prompt");
      // }else{
      //   this.$router.push("/Login");
      // }
      //如果价格为null,不让它跳到下一页
      if (!this.pricedata) {
        this.$createToast({
          txt: this.errorInfo,
          time: 3000,
          type: "txt"
        }).show();
        return false;
      } else {
        this.$router.push("/Login");
      }
    },
    //点击跳转到Travelinfo
    toTravelinfo() {
      this.$router.push("/Travelinfo");
    },
    //
    toPreviousPage(){
      this.$router.push("/Travelinfo");
    }
  },
  created() {},
  async mounted() {
    //区号
    var areacode = this.travelInfo.phone_country_code;
    //天数
    this.days = this.travelInfo.days;
    var params = {
      language: this.language,
      product_id: this.product_id,
      phone_country_code: areacode,
      merchant_id: this.MerchantId
    };
    const loading = this.$createToast({
      time: 0,
      txt: "Loading..."
    });
    loading.show();
    //获取产品详情
    const res = await this.$http.post(
      "/wesupromo/web/tpc/getProductDetails",
      params
    );
    this.$nextTick(() => {
      loading.hide();
    });
    this.data = res.data;
    console.log(res.data);
    this.setLogo(res.data.progress_logo);
    var strdata = this.data.remarks;

    // 报价
    let others_insured_list = [];
    const { count } = this.travelInfo;
    console.log(count);

    if (count.myself) {
      for (var i = 0; i < count.myself; i++) {
        others_insured_list.push({ relation: "2001" });
      }
    }
    if (count.spouse) {
      for (var i = 0; i < count.spouse; i++) {
        others_insured_list.push({ relation: "2002" });
      }
    }
    if (count.child) {
      for (var i = 0; i < count.child; i++) {
        others_insured_list.push({ relation: "2003" });
      }
    }
    if (count.friends) {
      for (var i = 0; i < count.friends; i++) {
        others_insured_list.push({ relation: "2004" });
      }
    }
    console.log(others_insured_list);
    
    var priceParams = {
      product_id: this.product_id,
      language: this.language,
      start_date: this.travelInfo.date.departureDate.replace(/\//g, "-"),
      end_date: this.travelInfo.date.returnDate.replace(/\//g, "-"),
      others_insured_list
    };
    console.log(priceParams);

    //日计划  月计划   年计划
    console.log(this.travelInfo.date.departureDate);
    console.log(this.travelInfo.date.returnDate);

    //调用接口获取价格
    this.$http
      .post("/wesupromo/web/product/getstarrquote", priceParams)
      .then(res => {
        console.log(res);
        this.price = res.data.premium;
        this.setPrice(this.price);
      })
      .catch(error => {
        console.log(error);
        if (error.status == 105) {
          this.errorInfo = error.msg;
        }
      });
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  padding-bottom: 260px;
  background-color: #fff;
  position: relative;
    .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(250, 250, 250);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
  .title {
    color: rgb(155, 155, 155);
    font-size: 30px;
    margin-top: 100px;
  }
  .travelead {
    color: #000;
    font-weight: 700;
    font-size: 44px;
    line-height: 52px;
    margin-bottom: 15px;
  }
  .budget {
    color: #000;
    font-weight: 600;
    font-size: 34px;
    line-height: 42px;
    margin-bottom: 10px;
  }
  .travel-insurance {
    color: rgb(142, 142, 147);
    font-size: 30px;
  }
  .headerimgs {
    width: 192px;
    height: 100px;
    margin: 30px 0;
    img {
      width: 100%;
      height: 100%;
      line-height: 192px;
    }
  }

  .price {
    text-align: right;
    color: rgb(12, 129, 255);
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 15px;
    margin-top: 35px;
    position: relative;
    height: 60px;
    .fontIcon {
      display: inline-block;
      text-align: left;
      position: absolute;
      left: 0;
      .iconfont {
        font-size: 35px;
        color: rgb(151, 151, 151);
        margin-left: 15px;
      }
    }
  }
  .projectImg {
    width: 100%;
    border-radius: 22px;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .people {
    // width: 100%;
    border-bottom: 1px solid rgb(209, 208, 208);

    line-height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > span {
      vertical-align: middle;
    }
    .toTravelinfo {
      padding: 0 20px;
    }
  }
  .days {
    font-size: 32px;
    color: rgb(74, 74, 74);

    > span {
      margin-left: 20px;
      margin-right: 15px;
    }
  }
  .line {
    font-size: 40px;
    color: rgb(151, 151, 151);
    margin-left: -100px;
  }
  .adults {
    display: inline-block;
    width: 47px;
    height: 47px;
    margin-right: -100px;
  }
  .adultsimg {
    width: 100%;
    height: 100%;
  }
  .friends {
    display: inline-block;
    width: 47px;
    height: 47px;
    margin-right: -100px;
  }
  .friendsimg {
    width: 100%;
    height: 100%;
  }
  .adultsnum {
    color: rgb(74, 74, 74);
    font-size: 30px;
    margin-right: -75px;
  }
  .friendsnum {
    color: rgb(74, 74, 74);
    font-size: 30px;
  }
  .cubeic-arrow {
    color: rgba(0, 0, 0, 0.25);
    font-size: 32px;
    font-weight: bold;
  }

  .trip {
    color: rgb(12, 129, 255);
    font-weight: 500;
    border-bottom: 1px solid rgb(209, 208, 208);
    line-height: 65px;
  }
  .key {
    font-size: 34px;
    font-weight: 550;
    line-height: 100px;
  }
  .key-content {
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    p {
      font-size: 30px;
      font-family: Arial, Helvetica, sans-serif;
      line-height: 45px;
    }
  }
  .features-wrap {
    white-space: pre-line;
    position: relative;
    .more {
      display: block;
      width: 100%;
      text-align: right;
      color: rgb(12, 129, 255);
    }
  }
  .features {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    /* autoprefixer:off */
    -webkit-box-orient: vertical;
    /* autoprefixer:on */
    -webkit-line-clamp: 4;
  }

  .customers {
    line-height: 75px;
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .starrs {
      display: inline-block;
      width: 65px;
      height: 65px;
      .starr {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .customer {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  .coverages {
    line-height: 75px;
    padding-bottom: 15px;
    display: flex;
    color: #000;
    justify-content: space-between;
    .covers {
      display: inline-block;
      width: 65px;
      height: 65px;
      .cover {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .coverage {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  .button-bar {
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 50%;
    width: 100%;
    height: 200px;
    transform: translateX(-50%);
    background-color: rgba(250, 250, 250);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
  }

  .proceed {
    width: 350px;
    height: 100px;
    line-height: 100px;
    border: none;
    border-radius: 10px;
    margin-top: 50px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
//媒体查询
@media screen and (min-width: 1050px) {
  .page-wrap {
    width: 900px;
    margin: 0 auto;
    box-sizing: border-box;
     .headTitle {
      width: 900px;
      margin-left: 50%;
      transform: translateX(-50%);
    }
    .button-bar {
      width: 900px;
    }
  }
}
</style>


